.searchbar .item {
    display: block;
    margin-bottom: 1px;
}

.searchbar .item:hover {
    cursor: pointer;
    background-color: whitesmoke;
    transition: all .03s linear;
}

.data {
    position: absolute;
    display: inline-table;
    margin: 5px;
}

.data .title{
    display: table-cell;
    font-size: 14px;
    font-weight: 500;
}

.item .data .year{
    display: table-footer-group;
    line-height: 30px;
    font-size: 12px;
    font-weight: 300;
}