.searchbar {
    background-color: white;
}

.searchbar .input {
    display: inline-block;
    font-size: 16px;
    font-weight: 400;
    margin: 5px;
    width: 245px;
    border-radius: 10px;
    background-color: rgba(200,200,200,0.05);
}

.searchbar .items {
    position: absolute;
    overflow: scroll;
    -ms-overflow-style: none;
    scrollbar-width: none;
    width: 250px;
    max-height: 385px;
    padding: 5px;
    border-radius: 10px;
    background-color: white;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
     0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    z-index: 2;
}

.searchbar .items::-webkit-scrollbar {
    display: none;
}

.searchbar .items .image{
    width: 50px;
    border-radius: 10px;
}